<template>
  <div class="article" :class="{ more }" :title="url" @click="click">
    <div v-if="hasMore" class="cont">+{{ more }}</div>
    <div class="descr" v-if="!hasMore">
      <img
        :src="`https://source.unsplash.com/random/1920x1080?t=${Math.random()}`"
        rel="nofollow"
        style="width: 100%"
      />
    </div>

    <div class="mb-0 head" v-if="!hasMore">
      {{ title }}
      <span class="text-end fst-italic author">â€“{{ author }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: ["item"],
  computed: {
    title() {
      return this.item.title;
    },
    date() {
      return this.item.date;
    },
    author() {
      return this.item.author;
    },
    description() {
      return this.item.description;
    },
    url() {
      return this.item.url;
    },
    more() {
      return this.item.more;
    },
    hasMore() {
      return this.item.more && true;
    },
  },
  methods: {
    click() {
      window.open(this.url, "_blank");
    },
  },
};
</script>

