export default {
  frameCount: 104,
  html: document.documentElement,
  canvas: document.getElementById("scroll-image"),
  context: null,
  img: new Image(),
  currentFrame(i) {
    return `${process.env.BASE_URL}img/frames/frame${i
      .toString()
      .padStart(3, "0")}.png`;
  },
  preloadImages() {
    for (let i = 0; i < this.frameCount; i++) {
      const img = new Image();
      img.src = this.currentFrame(i);
    }
  },
  init(elem) {
    this.canvas = elem || this.canvas;
    this.context = this.canvas.getContext("2d");

    this.img.src = this.currentFrame(0);
    this.canvas.width = 790;
    this.canvas.height = 1080;
    this.img.onload = () => {
      this.context.drawImage(this.img, 0, 0);
    };

    this.boundScrollListener =
      this.boundScrollListener || this.scrollListener.bind(this);
    window.addEventListener("scroll", this.boundScrollListener);

    this.preloadImages();
  },
  deInit() {
    window.removeEventListener("scroll", this.boundScrollListener);
  },
  boundScrollListener: null,
  scrollListener() {
    let middle =
      (this.canvas.getBoundingClientRect().top +
        this.canvas.getBoundingClientRect().bottom) /
      2 /
      window.innerHeight;
    const scrollFraction = (1 - middle) * 2;
    const frameIndex = Math.max(
      0,
      Math.min(this.frameCount - 1, Math.ceil(scrollFraction * this.frameCount))
    );

    requestAnimationFrame(() => this.updateImage(frameIndex));
  },

  updateImage(index) {
    this.img.src = this.currentFrame(index);
    this.context.drawImage(this.img, 0, 0);
  },
};
